.footer {
    position: relative;
    height: 50px;
}

.footerBlock {
    display: flex;
    position: absolute;
    bottom: 0;
    left: 35%;
}

.formControl {
    border: none;
    border-radius: 5px;
    color: white;
    box-shadow: 0 0 32px 0 rgba(0, 40, 120, .35);
    background-image: linear-gradient(45deg, #6ab1d7 0%, #33d9de 50%, #002878 100%);
    background-position: 100% 0;
    background-size: 200% auto;
    transition: .5s;
    font-size: 17px;
}

.select {
    height: 40px;
}

.formControl:hover {
    box-shadow: 0 0 0 0 rgba(0, 40, 120, 0);
    background-position: 0 0;
}


.buttonLoad {
    transition: .5s;
    padding: 10px 25px;
    min-height: 26px;
    margin-left: 35px;
    border-radius: 4px;
    border: 1px solid #6ab1d7 ;
}